<p><br></p>
<p><span class="rvts58">气元素学派（</span><span class="rvts59">Aerotheurge</span><span class="rvts58">）：</span>
<br><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber168.jpg"><span class="rvts28"></span></p>
<p><span class="rvts21">气元素学派技能一共有</span><span class="rvts20">24</span><span class="rvts21">个，所有的气元素学派技能都基于智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）属性造成额外的气元素伤害加成。</span>
<br><span class="rvts21">气元素学派中会有一些非常有用的辅助型技能，可以使角色进行传送，眩晕，为队友提升护盾和提供增益</span><span class="rvts20">BUFF</span><span class="rvts21">，祛除一些减益</span><span class="rvts20">BUFF</span><span class="rvts21">。</span>
<br><span class="rvts21">值得注意的是，气元素学派算是一个比较可靠的学派分支，因为在原罪</span><span class="rvts20">2</span><span class="rvts21">中，大多数敌人对气元素的抵抗较低。</span>
<br><span class="rvts47"></span>
<br><span class="rvts29">气元素学派技能书可以从以下</span><span class="rvts33">NPC</span><span class="rvts29">中买到（或学到）：</span>
<br><span class="rvts20">1. </span><span class="rvts21">盖恩（</span><span class="rvts20">Gawin</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">欢乐堡</span>
<br><span class="rvts20">2. </span><span class="rvts21">欧维斯（</span><span class="rvts20">Ovis</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">浮木城</span>
<br><span class="rvts20">3. </span><span class="rvts21">格拉泰纳（</span><span class="rvts20">Gratiana</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿玛迪亚避难所</span>
<br><span class="rvts20">4. </span><span class="rvts21">阿尔麦拉（</span><span class="rvts20">Almira</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">复仇女士号</span>
<br><span class="rvts20">5. </span><span class="rvts21">精灵暴风吟唱者（</span><span class="rvts20">Elf StormChanter</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">精灵营地，死神海岸，无名岛</span>
<br><span class="rvts20">6. </span><span class="rvts21">蜥蜴做梦者（</span><span class="rvts20">Lizard Dreamer</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">费勒斯神庙南部，无名岛</span>
<br><span class="rvts20">7. </span><span class="rvts21">商人崔迪（</span><span class="rvts20">Trayde The Merchant</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">泰森地留斯神庙，无名岛</span>
<br><span class="rvts20">8. </span><span class="rvts21">商人阿若瓦伊（</span><span class="rvts20">Trader Avarae</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
<br><span class="rvts20">9. </span><span class="rvts21">商人弗利尔（</span><span class="rvts20">Trader Friel</span><span class="rvts21">）</span><span class="rvts20"> -- </span><span class="rvts21">阿克斯</span>
